import React from 'react'
import Wrapper from '../../hoc/Wrapper'
import Controls from '../../components/controls/Controls'
import Modal from '../../components/ui elements/modal/Modal'
import Order from '../../components/order/Order'
const prices={
    product1:59,
    product2:12,
    product3:56,
    product4:9,
}

class Shopping extends React.Component{
    state={
        products:{
            product1:0,
            product2:0,
            product3:0,
            product4:0,
        },
        totalPrice:0,
        purchased:false
    }

    addProductHandler=(type)=>{
      
      const previousCount=this.state.products[type]
      const updatedCount=previousCount+1
      const updatedProducts={
          ...this.state.products,
      }
      updatedProducts[type]=updatedCount
      const priceAdd=prices[type]
      const prevPrice=this.state.totalPrice
      const newPrice=prevPrice+priceAdd
      this.setState({totalPrice:newPrice , products:updatedProducts})
    }

    removeProductHandler=(type)=>{
        
        const previousCount=this.state.products[type]
        const updatedCount=previousCount-1
        if(this.state.totalPrice>0 && updatedCount>=0){
        const updatedProducts={
            ...this.state.products,
        }
        updatedProducts[type]=updatedCount
        const priceSub=prices[type]
        
        const prevPrice=this.state.totalPrice
        const newPrice=prevPrice-priceSub
    
        if(newPrice>=0){
        this.setState({totalPrice:newPrice , products:updatedProducts})
        }

    }
     
    }
purchasedHandler=()=>{
    this.setState({purchased:true})
}

modalCloseHandler=()=>{
    this.setState({purchased:false})
}

purchaseContinueHandler=()=>{
    console.log('purchaseHandler')
}

    render(){
        return(
            <Wrapper>
              <Modal show={this.state.purchased} modalClose={this.modalCloseHandler}>
                  <Order products={this.state.products} continue={this.purchaseContinueHandler} cancel={this.modalCloseHandler} price={this.state.totalPrice} />
              </Modal>
                <Controls productAdd={this.addProductHandler} productRemove={this.removeProductHandler} price={this.state.totalPrice} order={this.purchasedHandler}/>
            </Wrapper>
        )
    }
}

export default Shopping